.App {
  text-align: center;
}

.App-logo {
  height: 5vmin;
  pointer-events: none;
  padding: 10px;
}

.App-image {
  height: 40vmin;
  pointer-events: none;
  padding: 10px;
}

.App-header {
  background-color: #ffffff;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: black;
}

#impressum > h6 {
  font-size: 0.75vmin;
}

#impressum > p {
  font-size: 0.75vmin;
}


#impressum > p > a:link, a:visited, a:active {
  color: black; 
  background-color: transparent; 
  text-decoration: underline;
}

#impressum > p > a:hover {
  color: rgb(80, 80, 80); 
  background-color: transparent; 
  text-decoration: underline;
}